const socket = io("http://localhost:4000", {
    query: {token: "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpZCI6IjVjY2ZhM2M2MTdhMmQ3NzBhOTRjZTViNSIsImlhdCI6MTU1NzM4Mzk2MywiZXhwIjoxNTU3NDcwMzYzfQ.OOzKP5SeBN4b9C6jXBTtMuY__OGJr-TPRNea-5iD9W0"}
});
const functions= new Set();
export const Chat = () => {
    const [listMessage, setListMessage] =  useState([]);
    const [Threads, setThreads ] = useState([]);
    const [message, setMessage] = useState("");
    const [messageSend, SetmessageSend] = useState("");
    const [currentThread, setCurrentThread] = useState({});
    
    const registerHandler = useCallback(() => {
        socket.on('messagereceived', (message) => {
            onMessageReceived(message);
        })
        
    },[message])  
    const onMessageReceived = (message) => {
      
        setListMessage(listMessage.concat(message));
        
         let threadChange =Threads.map(thread => {
            if(thread._id === message.thread_id) {
                thread.current_message.message = message.message;
                thread.current_message.date = message.created_at;
            }
            return thread;
        })
        setThreads(threadChange);
    }
    const sendMesage = useCallback( (message,threadId) => {
        socket.emit('message',message,threadId, (error) => console.log(error))
        
    },[messageSend])
    const getMessageAndSet = useCallback((threadId,skip) => {
        socket.emit('join', threadId,skip,(error,chatHistory) => {
            if (error) {
                console.log(error);
            }
            chatHistory.reverse()
            setListMessage(chatHistory);
         });
    
    },[currentThread])
    const refBoxMessage = useRef(null);
    useEffect(() => {

        socket.emit('threads','5cd28daf09c42a0a52ab8832',(error, threads) => {
            if (error) {
                console.log(error);
            }
         
            getMessageAndSet( threads[0]._id,0);
            setCurrentThread(threads[0]);
            setThreads(threads);
         
        })
    },[]);
 
    useEffect(() => {
        
        const scrollBottom = () => {
            
            refBoxMessage.current.scrollTo (0,refBoxMessage.current.scrollHeight);  
        }
        
        scrollBottom();

    },[listMessage])
  

    useEffect(() => {
        registerHandler()
        return () => {
            socket.off('messagereceived')
      }
    },[message])
    const  handleScroll = () => {
     
        if (refBoxMessage.current.scrollTop === 0) {
           
           socket.emit('join', currentThread._id,listMessage.length,(error,chatHistory) => {
            if (error) {
                console.log(error);
            }

            chatHistory.reverse()
           
            if(chatHistory.length !== 0) {
                setListMessage([...chatHistory,...listMessage]);
            }
         
        
         });
        }
    }
  

    const test = useCallback((thread) => {
        getMessageAndSet(thread._id);
        setCurrentThread(thread);
    }, [currentThread])
    return (
       
        <div className="container-fluid">
            {console.log(refBoxMessage)}
            
            <div className="row">
                <div className="col-md-3 col-sm-3 col-3">
                   
                    <ContainerList>
                    {Threads.map(thread => {
                       
                        return (
                        <ListThread onClick={() => {
                            test(thread)
                        }}>
                            <ImgThread>
                                <Img src={Avatar} />
                            </ImgThread>
                            <WarperThread>
                                <WarrperUser>
                                    <InfoUser>{thread.name}</InfoUser>
                                    
                                    <div> {thread.current_message.date  }</div>
                                </WarrperUser>
                                <MessageInfo>
                                    <div>{thread.current_message.message}</div>
                                </MessageInfo>
                            </WarperThread>

                        </ListThread>
                        )
                    })}
                   
                    </ContainerList>
                </div>
                <div className="col-md-6 col-sm-6 col-6">
                    <WarperMessage>
                        <BoxMessage onScroll={handleScroll} ref={refBoxMessage}>
                            {listMessage.map(message => {
                              
                                return (      
                                 <div >
                                    <MessageItem >
                                        <MessageContent>
    
                                            <MessageContentText>
                                                {message.message}
                                            </MessageContentText>
                                            <MessageContentImageRight>
                                                <Img src={Avatar} />
                                            </MessageContentImageRight>
                                        </MessageContent>
                                    </MessageItem>
                                </div>)
                            })}
                       
                        </BoxMessage>
                        <BoxSendMessage>
                            <input value={message} onChange={(e) => {
                                 setMessage(e.target.value);
                            }} onKeyDown={(e) => {
                                    if (e.key === 'Enter') {
                                        sendMesage(message,currentThread); 
                                        SetmessageSend(!messageSend);
                                        setMessage("");  
                                    }
                                  
                            }}/>
                        </BoxSendMessage>

                    </WarperMessage>
                </div>
                <div className="col-md-3 col-sm-3 col-3">
                     <WarperClientOfThread>
                         <ContainerClientOfThread>
                            <ContainerHeaderThread>
                                <ImgThread>
                                    <Img src={Avatar} />
                                </ImgThread>
                                <HeaderThread>
                                    {console.log(currentThread)}
                                    {currentThread.name}
                                </HeaderThread>
                            </ContainerHeaderThread>
                            <ContainerOfThread>
                                <ContentOfThread>
                                    <ListItems>
                                        {currentThread.participants !== undefined  && currentThread.participants.map(user => {
                                            return (
                                                <Items >
                                                    <ImgThread>
                                                        <Img src={Avatar} />
                                                    </ImgThread>
                                                    <UserName>
                                                        {user.name}
                                                    </UserName>
                                                    
                                                </Items>
                                            )
                                        })}
                                    </ListItems>
                                </ContentOfThread>
                            </ContainerOfThread>
                         </ContainerClientOfThread>
                     </WarperClientOfThread>
                </div>
            </div>
        </div>
    )
}

