import React, { useState } from 'react';
import { InputForm } from './InputForm';
import { LinkButton } from './LinkButton';
import Logo from '../image/logo.jpeg';
import '../sass/styles.scss';


export default function Login(props) {
    const {loginRequest} = props;
    const [email, updateEmail] = useState("");
    const [password, updatePassword] = useState("");

    function handleLogin(e) {
        e.preventDefault();
        loginRequest({email, password},props.history);
    }
    return (
        <div className="container">
            <div className="row justify-content-center" >
                <div className="col-xl-12 col-lg-12 col-md-9">
                    <div className="shadow-lg my-5 ">
                        <div className="row">
                            <div className="col-lg-6 d-none d-lg-block">
                                <img src={Logo} />
                        </div>
                            <div className="col-lg-6">
                                <div className="p-5">
                                    <div class="text-center">
                                        <h1 class="h4 text-gray-900 mb-4">Welcome Back!</h1>
                                    </div>
                                    <div>
                                        <InputForm type="email" onChange={(e) => updateEmail(e.target.value)} placeholder="Enter Email Address..." />
                                        <InputForm type="password" onChange={(e) => updatePassword(e.target.value)} placeholder="Password..." />
                                        <LinkButton onClick={handleLogin} className="btn btn-primary btn-block btn-user"> Login </LinkButton>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}