import React, { useEffect, useState } from 'react';
import Modal from './Modal'
import { Table } from './Table';
import { Pagination } from './Pagination';
import ModalFooterProduct from '../container/FooterModal'
import { ModalContentProduct } from './Modal/FooterModal'

const HeadProduct = ["Mã hàng hóa", "Tên hàng", "Giá bán", "Tồn kho"];
export default function DashBroad(props) {
    const [showModal, setModal] = useState(false);
    const [currentproducts, setProducts] = useState([]);
    const [currentPages, setCurrentPage] = useState(null);
    const [currentProduct, setProduct] = useState({});
    const { authRequest, productRequest } = props;
    const { product } = props.product.payload;
    const { isFetching } = props.product;
    const { history } = props;

    useEffect(() => {
        productRequest();
        authRequest(history);
    }, [])
    useEffect(() => {
        const current_product = () => {
            let offset = (currentPages - 1) * 10;
            let temp = product.slice(offset, offset + 10);
            setProducts(temp);
        }
        current_product();

    }, [currentPages])
    useEffect(() => {
        const current_product = () => {
            let offset = (currentPages - 1) * 10;
            let temp = product.slice(offset, offset + 10);
            setProducts(temp);
        }
        current_product();
        return () => { current_product() }
    }, [product])
    function onPageChanged(data) {
        setCurrentPage(data);
    }
    useEffect(() => {
        console.log(currentProduct);
    }, [currentProduct])

    return (


        <div>


            <Modal
                isShowing={showModal}
                setShow={setModal}
                content={ModalContentProduct}
                Footer={ModalFooterProduct}
                product={currentProduct}
                onProductChange={setProduct}
                header="Thêm hàng hóa">

            </Modal>

            {isFetching ? null :
                <div className="container">
                    <Table thead={HeadProduct} >
                        {currentproducts.map((product) => (
                            <tr>

                                <th>{product.title}</th>
                                <th>{product.retailPrice}</th>
                                <th>{product.salePrice}</th>
                                <th> <button onClick={() => { setModal(!showModal); setProduct(product) }} className="btn btn-primary"> EDIT</button> </th>
                            </tr>
                        ))}
                    </Table>

                    <Pagination totalRecords={product.length} pageLimits={10} pageNeighbours={2} onPageChanged={onPageChanged} />
                </div>
            }
        </div>
    )
}